<template>
  <div class="zizhushegnbao">
    <meta
      name="viewport"
      content="initial-scale=1.0, maximum-scale=1, minimum-scale=1, user-scalable=no,uc-fitscreen=yes"
    />
    <div class="title">
      <van-button
        style="background-color: #565353 !important; border: 0"
        type="primary"
      /><span style="margin: 0 auto">场所码</span>
    </div>
    <div class="main" v-show="!show">
      <van-field
        v-model="formValute.real_name"
        class="zizhushengbaoFiled"
        placeholder="请输入姓名"
        label-class="zizhushengbaoLabel"
        label="姓名："
      />
      <van-field
        v-model="formValute.id_card"
        class="zizhushengbaoFiled"
        @input="
          () => {
            this.formValute.id_card = this.formValute.id_card.replace(
              /[^\w\.\/]/g,
              ''
            );
          }
        "
        label-class="zizhushengbaoLabel"
        :rules="[{ validator: asyncValidator, message: '请输入正确的证件号' }]"
        placeholder="请输入正确的身份证号"
        label="身份证："
      />
      <van-field
        v-model="formValute.phone"
        class="zizhushengbaoFiled"
        label-class="zizhushengbaoLabel"
        center
        type="number"
        placeholder="请输入手机号"
        clearable
        label="手机号："
      >
      </van-field>
      <van-field
        v-model="smsCode"
        class="zizhushengbaoFiled"
        label-class="zizhushengbaoLabel"
        center
        :placeholder="
          formValute.phone === '' ? '请先输入手机号' : '请输入验证码'
        "
        :readonly="!formValute.phone"
        clearable
        label="验证码："
      >
        <template #button>
          <van-button size="small" type="primary" @click="getsms" v-if="!smsIF"
            >获取验证码</van-button
          >
          <van-button size="small" type="primary" class="gray" disabled v-else
            ><span>{{ smsdJS }}后重新获取</span></van-button
          >
        </template>
      </van-field>
      <van-button
        type="primary"
        @click="onSubmit"
        block
        :loading="loadingBtn"
        loading-text="加载中..."
        class="submit"
        native-type="submit"
        >提交</van-button
      >
    </div>
    <!-- <van-overlay :show="overlay">
      <van-loading
        size="24px"
        vertical
        style="
          position: fixed;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        "
        >正在校验姓名和身份证号码</van-loading
      >
    </van-overlay> -->
  </div>
</template>

<script>
import VDistpicker from 'v-distpicker'
import {
  Toast
} from 'vant'
import {
  getSmsCode, uploadImg
} from '@/common/api/api.js'
import { mixinsChaXun } from '../common/mixinsChaXun'
import { postPlaceDeclare, scanCodePost } from '../common/api/api'
import cookies from '../lib/util.cookie'
import { Debounce } from '../common/debounceThrottle'
export default {
  components: { VDistpicker },
  mixins: [mixinsChaXun],
  data () {
    return {
      loadingBtn: false,
      loading: true,
      smsCode: '',
      show: false,
      showLink: false,
      fileList: [],
      formValute: {
        phone: ''
      },
      smsIF: false,
      smsdJS: 60,
      codeCsm: '',
      msg: 1 // 信息
    }
  },
  created () {
    console.log(this.$route)
    if (this.$route.query.place_code) {
      this.place_code = this.$route.query.place_code
      console.log(this.place_code)
    }
    const formValute = cookies.get('formValute')
    if (typeof formValute === 'string') {
      JSON.parse(formValute) ? this.formValute = JSON.parse(cookies.get('formValute')) : this.formValute = ''
    }
    const sign = cookies.get('sign')
    console.log(sign, 'sign')
    // 判断签名是否存在，若存在跳转详情页面
    if (sign) {
      this.signPad(sign)
    }
  },
  methods: {
    signPad (sign) {
      console.log(this.formValute.place_code)
      scanCodePost({
        place_code: this.$route.query.place_code,
        sign
      }).then(res => {
        if (res.code === 200) {
          const seconds = 24 * 3600
          const expires = new Date(new Date() * 1 + seconds * 1000)
          cookies.remove('sign')
          cookies.set('sign', res.data.sign, {
            expires
          })
          this.$router.replace({
            path: '/csmXq'
          })
        } if (res.code === 400) {
          this.$router.replace({
            path: '/error'
          })
        }
      })
    },
    onSubmit () {
      // console.log(this.shenbaoYanzheng)
      const _this = this
      _this.shenbaoYanzheng()
      // 要执行的函数
    },
    postSubmit () {
      const formValute = JSON.parse(JSON.stringify(this.formValute))
      formValute.sms_code = this.smsCode
      formValute.place_code = this.place_code
      const seconds = 24 * 3600
      const secondsForm = 7 * 24 * 3600
      const expires = new Date(new Date() * 1 + seconds * 1000)
      this.loadingBtn = true
      postPlaceDeclare(formValute).then(res => {
        console.log(res)
        if (res.code !== 200) {
          Toast.fail(res.msg)
          this.loadingBtn = false
        } else {
          cookies.set('sign', res.data.sign, {
            expires
          })
          this.$router.replace({
            path: `/csmXq`
          })
          const formValuteData = JSON.parse(JSON.stringify(this.formValute))
          delete formValuteData.place_code
          cookies.set('formValute', JSON.stringify(formValuteData), {
            expires: new Date(new Date() * 1 + secondsForm * 1000)
          })
        }
      })
    },
    closeSparingContent (show) {
      this.show = show
      this.isHide = false
    },
    onFailed () { },
    onConfirm_idCardType (idCardType) {
      console.log(idCardType, 'SA')
      this.formValute.card_type = idCardType.value
      this.formValute.card_type_text = idCardType.text
      this.showPicker_idCardType = false
    },
    onCancel_idCardType () {

    },
    getsms () {
      getSmsCode({
        phone: this.formValute.phone
      }).then(res => {
        if (res.code === 200) {
          this.smsIF = true
          this.showDiagler = true
          this.djsSms()
          Toast.success(res.msg)
        } else {
          Toast.fail(res.msg)
        }
      })
    },
    djsSms () {
      this.smsdJS = this.smsdJS - 1
      this.smsIF = true
      if (this.smsdJS <= 0) {
        this.smsIF = false
        this.smsdJS = 60
        clearTimeout(this.setTime)
      } else {
        this.setTime = setTimeout(() => {
          this.djsSms()
        }, 1000)
      }
    },
    asyncValidator (val) {
      if (this.formValute.card_type_text === '身份证') {
        return /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/.test(val)
      } else {
        return true
      }
    }
  }
}
</script>
<style>
.zizhushegnbao .van-field__button {
  max-width: 40%;
}
.zizhushegnbao ::-webkit-input-placeholder {
  color: red !important;
  font-weight: 500 !important;
}

/* Firefox 4-18 */
.zizhushegnbao :-moz-placeholder {
  color: red !important;
  font-weight: 500 !important;
}

/* Firefox 19-50 */
.zizhushegnbao ::-moz-placeholder {
  color: red !important;
  font-weight: 500 !important;
}
.zizhushengbaoLabel_other {
  /* color: red !important; */
  width: 8.5rem;
}
/* - Internet Explorer 10–11
- Internet Explorer Mobile 10-11 */
.zizhushegnbao :-ms-input-placeholder {
  color: red !important;
  font-weight: 500 !important;
}

/* Edge (also supports ::-webkit-input-placeholder) */
.zizhushegnbao ::-ms-input-placeholder {
  color: red !important;
  font-weight: 500 !important;
}

/* CSS Working Draft */
.zizhushegnbao ::placeholder {
  color: red !important;
  font-weight: 500 !important;
}
.distpicker-address-wrapper .address-container ul li.active {
  color: red !important;
}
.distpicker-address-wrapper .address-container ul li {
  font-size: 14px;
}
.van-dialog__content {
  display: flex;
  justify-content: center;
}
.van-uploader__upload {
  width: 10rem;
  height: 10rem;
}
.distpicker-address-wrapper {
  color: black;
}
.cellClass_spring > .van-cell__value {
  min-width: 75%;
}
.van-field__control {
  font-size: 14px !important;
}
.van-nav-bar__right {
  padding: 5px;
}
.van-tabs--card > .van-tabs__wrap {
  height: 36px;
}
.van-tabs__nav--card {
  margin: 0;
  border: none;
  height: 36px;
}
.van-tabs__nav--card .van-tab {
  color: #333333;
  border-right: none;
  height: 36px;
}
.van-tabs__nav--card .van-tab.van-tab--active {
  color: #fff;
  background-color: #ff5318;
}
.zizhushengbaoLabel {
  /* color: red !important; */
}
.van-field__label {
  width: auto;
}
input:disabled {
  background-color: #d5d5d5 !important;
}
</style>
<style  scoped>
.main {
  width: 100%;
}
textarea {
  text-align: right;
}
.van-form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.submit {
  margin-top: 1.8rem;
  border-radius: 0.3125rem;
}
.title {
  font-size: 18px;
  width: 95%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #fff;
  margin-bottom: 1rem;
}
.zizhushegnbao {
  min-height: 100vh;
  background: #565353;
  display: flex;
  /* justify-content: center; */
  /* 禁止缩放放大字体 */
  -webkit-text-size-adjust: none;
  align-items: center;
  flex-direction: column;
  padding: 0.9375rem;
}
.zizhushengbaoFiled {
  border-radius: 0.375rem;
  background-color: #d5d5d5;
  /* width: 95%; */
  display: flex;
  align-items: center;
  height: 2.8125rem;
  margin-bottom: 0.9375rem;
}
.canYouHandBaoGao {
  color: #68b0f9;
  font-size: 1rem;
  margin-right: 2rem;
}
.dibu {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.divwrap {
  height: 400px;
  overflow-y: auto;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
}
.divwrap >>> .distpicker-address-wrapper {
  color: #999;
}
.divwrap >>> .address-header {
  position: fixed;
  bottom: 400px;
  width: 100%;
  background: #000;
  color: #fff;
}
.divwrap >>> .address-header ul li {
  flex-grow: 1;
  text-align: center;
}
.divwrap >>> .address-header .active {
  color: #fff;
  border-bottom: #666 solid 8px;
}
.divwrap >>> .address-container .active {
  color: #000;
}
.pickerUploderPopup {
  width: 80%;
  height: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: rgb(225, 226, 225);
}
.fixedPOstion {
  position: absolute;
  top: 0;
  left: 0;
  /* min-height: 100vh; */
  /* height: 100%; */
  background: #565353;
  width: 100%;
  padding: 0 0.5rem;
  box-sizing: border-box;
  padding-top: 0.3125rem;
}
</style>



