import { Toast } from 'vant'
import cookies from '../lib/util.cookie'
export const mixinsChaXun = {
  data () {
    return {
      overlay: false,
      show: false,
      id_verify_result: 1,
      isHide: false
    }
  },
  created () {
  },
  methods: {
    shenbaoYanzheng () {
      const that = this
      let formValute = this.formValute
      if (!formValute.real_name) {
        Toast.fail('请输入姓名')
        return false
      }
      var pat = new RegExp('[^a-zA-Z0-9\_\u4e00-\u9fa5]', 'i') // 特殊字符
      let realNameChilaReg = /\d+|\w+/ // 字母数字
      if (pat.test(formValute.real_name)) {
        console.log(formValute.real_name, pat.test(formValute.real_name))
        Toast.fail('姓名不能包含特殊字符')
        return false
      }
      if (this.formValute.card_type === 'id') {
        if (realNameChilaReg.test(formValute.real_name)) {
          Toast.fail('姓名不能包含字母或数字')
          return false
        }
      }
      if (!formValute.id_card) {
        Toast.fail('请输入证件号')
        return false
      }
      let idCardReg = /^[1-9]\d{5}(18|19|20)\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/// 先用正则判断是否为正确的格式
      if (this.formValute.card_type === 'id') {
        if (!idCardReg.test(formValute.id_card)) {
          Toast.fail('证件号格式错误')
          return false
        } else {
          console.log('校验')
        }
      }
      if (!formValute.phone) {
        Toast.fail('请输入手机号')
        return false
      }
      let myreg = /^1\d{10}$/
      if (!myreg.test(formValute.phone)) {
        Toast.fail('手机号格式错误')
        return false
      }
      if (!that.smsCode) {
        Toast.fail('请输入验证码')
        return false
      }
      this.postSubmit()
    },
    linkTo () {
      const seconds = 24 * 3600
      const expires = new Date(new Date() * 1 + seconds * 1000)
      cookies.set('formValute', JSON.stringify(this.formValute), {
        expires: expires
      })
      this.$router.push({
        path: '/csmXq'
      })
    },
    // 图片大小校验
    onOversize (file) {
      console.log(file)
      Toast('文件大小不能超过 1MB')
    }
  }
}



